import React, {Component} from 'react';
import { Grid, Cell, List, ListItem, ListItemContent } from 'react-mdl';

class Contact extends Component{
	render(){
		return(
			<div className="contact-body">
				<Grid className="contact-grid" style={{overflow: 'auto'}}>
					<Cell col={6}>
						<h2>VAISHAKH KP</h2>
						<img 
							src={require('./avatar-3.jpeg')}
							alt="avatar"
							style={{height:"250px", borderRadius:"50px"}}
						/>
						<p style={{ width:"75%", margin:"auto", paddingTop: "20px", fontSize:"20px"}}>
							"If you want to create a responsive website or any python web applications here I'am."
						</p>
					</Cell>
					<Cell col={6}>
						<h2>Contact Me</h2>
						<hr/>
						<div className="contact-list">
							<List>
  								<ListItem>
    								<ListItemContent style={{fontSize: '30px', fontFamily:'Anton'}}>
    								<i className="fa fa-phone-square" aria-hidden="true"/>
    								(+91) 9400993041
    								</ListItemContent>
 								 </ListItem>

 								
 								 <ListItem>
    								<ListItemContent style={{fontSize: '30px', fontFamily:'Anton'}}>
    								<i className="fa fa-envelope" aria-hidden="true"/>
    								kpvaisakh@gmail.com
    								</ListItemContent>
 								 </ListItem>

 								
  								
							</List>
						</div>
					</Cell>
				</Grid>
			</div>
		)
	}
}
export default Contact;