import React, {Component} from 'react';
import {Grid, Cell} from 'react-mdl';

class About  extends Component{
	render(){
		return(
			<div>
				<Grid className="about-me">
					<Cell col={12}>
						<img src={require('./avatar-2.jpeg')}
				 		alt="avatar"
				 		className="avatar-img"
						/>
						<div className="box">
							<h2>Hi there, I am Vaishakh kp</h2>
							<h4>Respectful self-motivator gifted at finding reliable solutions for software issues. Experienced in scripting automated conversion web tools, Front end UI development, and offering skills in PHP, Python, HTML, CSS, and JavaScript. Fluent in English and accustomed to working with cross-cultural, global teams.  </h4>
						</div>
					</Cell>
				</Grid>
			</div>
		)
	}
}
export default About;