import React, {Component} from 'react';
import { Grid, Cell } from 'react-mdl';



class Landing extends Component{
	render(){
		return(
			<div style={{width: '100%', margin: 'auto'}}>
				<Grid className="landing-grid" style={{overflow: 'auto'}}>	
				 	<Cell col={12}>
				 		<img src={require('./avatar-2.jpeg')}
				 		alt="avatar"
				 		className="avatar-img"
				 		/>

				 		<div className="banner-text">
				 			<h1>Hello, I am Vaishakh</h1>
				 			<h2>I'm a Full Stack Web Developer.</h2>
				 		<hr/>
				 		<p>HTML/CSS | REACT.JS | PYTHON/DJANGO | MYSQL | JAVASCRIPT | REST API/SWAGGER</p>
				 		
				 		<div className="social-links">
				 			
				 			{/* LinkedIn */}
				 			<a href="http://google.com" rel="noopener noreferrer" target="_blank">
				 				<i className="fa fa-linkedin-square" aria-hidden="true" />
				 			</a>

				 			{/* Github */}
				 			<a href="http://google.com" rel="noopener noreferrer" target="_blank">
				 				<i className="fa fa-github-square" aria-hidden="true" />
				 			</a>

				 			{/* Facebook */}
				 			<a href="http://google.com" rel="noopener noreferrer" target="_blank">
				 				<i className="fa fa-facebook-square" aria-hidden="true" />
				 			</a>

				 			{/* google */}
				 			<a href="http://google.com" rel="noopener noreferrer" target="_blank">
				 				<i className="fa fa-google-plus-square" aria-hidden="true" />
				 			</a>
				 		
				 		</div>
				 		</div>
				 	</Cell>
				 </Grid>
			</div>
		)
	}
}
export default Landing;